@import url(https://fonts.googleapis.com/css?family=Lato);
.link {
  text-decoration: none;
  color: inherit;
}
html,
body {
  max-width: 100%;
  overflow-x: hidden;
  margin: 0px;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
}

h1 {
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
}

.countdown-wrapper {
  position: relative;
  top: 35vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.countdown-item {
  color: #111;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  line-height: 30px;
  margin: 10px;
  padding-top: 10px;
  position: relative;
  width: 100px;
  height: 100px;
}

.countdown-item span {
  color: #333;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.countdown-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
}

.cnt-container {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-image: url(/static/media/bg.b00c099b.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  /* background-color: rgba(255, 255, 255, 0.2); */
}

.spl-txt {
  position: relative;
  top: 50vh;
  font-size: 1.8em;
  width: 100%;
  color: #111;
  text-align: center;
}

.launch {
  cursor: pointer;
  font-size: 2em;
  position: relative;
  top: 45vh;
  left: calc(50vw - 50px);
  padding: 5px 10px;
  border-radius: 5px;
  border: none;
  background-color: rgb(214, 67, 111);
  color: #eee;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  -webkit-animation: App-logo-spin infinite 15s linear;
          animation: App-logo-spin infinite 15s linear;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

